.g-table-display {
	&.scroll {
		@include overflow-scroll(default, dark, large, x);
	}

	table {
        width: 100%;
		border-collapse: collapse;

		thead {
			th {
				padding: 16px;
				border-bottom: 1px solid #dfdfdf;
				background-color: #f7f7f7;
				font-size: 14px;
			}
		}

		tbody {
			th, td {
				padding: 12px 20px;
				border-bottom: 1px solid #ebebeb;
				font-size: 14px;
				text-align: center;
			}

			th {
				font-weight: 600;
			}

			.align-a {
				text-align: left;
			}
		}
	}
}

.g-color-palettes {
	.data-list {
		display: flex;
		flex-wrap: wrap;
		gap: 4px;

		.data-item {
			flex: 1 0 1;
			display: flex;
			flex-direction: column;
			justify-content: center;
			align-items: center;
			gap: 4px;
			width: 130px;
			height: 130px;
			padding: 20px 10px;
			box-sizing: border-box;
			border-radius: 50%;

			.data-name {
				font-size: 14px;
				line-height: 1.5;
				text-shadow: 0px 1px 5px #474747;
			}

			.data-code {
				font-size: 14px;
				line-height: 1.4;
				text-align: center;
				text-shadow: 0px 1px 5px #474747;
			}

			@each $palette-name, $palette in $COLOR-PALETTES {
				@each $name, $color in $palette {
					&.#{$palette-name}-#{$name} {
						background-color: $color;
						color: var(--color-base-100);
					}
					// @debug "background-color:" $color;
				}
			}
		}
	}
}

.g-icon-list {
	.data-list {
		display: flex;
		flex-wrap: wrap;
		gap: 24px;
		padding: 0 20px;
		border-radius: 4px;
		background-color: #f7f7f7;

		.data-item {
			width: 64px;

			&:hover {
				.data-name {
					&::before {
						background-color: #000;
					}
				}
			}
			
			.data-name {
				display: flex;
				align-items: center;
				justify-content: center;
				flex-direction: column;
				gap: 12px;
				padding: 20px 0;
				font-size: 14px;
				text-align: center;
				word-break: break-all;

				&::before {
					content: '';
					display: block;
					width: 20px;
					height: 20px;
					min-height: 20px;
					mask-repeat: no-repeat;
					mask-position: 50%;
					mask-size: cover;
					background-color: #6a6a6a;
					transition: background-color cubic-bezier(0.075, 0.82, 0.165, 1);
				}
			}

			@each $iconName, $color in $iconList {
				&.#{$iconName} {
					.data-name {
						&::before {
							mask-image: icon($iconName);
						}
		
						&:hover {
							&::before {
								mask-image: icon($iconName);
							}
						}
					}
				}
			}
		}
	}
}

.g-folder-tree {
	display: flex;
	gap: 80px;

	p {
		font-size: 18px;
		font-weight: 700;
	}

	.tree-list {
		padding-left: 10px;

		li {
			position: relative;
			line-height: 1.7;
			padding: 0 0 0 44px;

			&::before {
				content: '└ ';
				position: absolute;
				top: 2px;
				left: 0;
				display: block;
			}

			&::after {
				content: '🗂️ ';
				position: absolute;
				top: 1px;
				left: 20px;
				display: block;
			}
		}

		.tree-depth {
			padding: 0 0 0 0;

			&:last-child {
				li {
					margin-left: -20px;
				}
			}
		}
	}
}

.highlight-js {
	margin: 0;
	padding: 0;

	.hljs {
		padding: 16px;
	}

	code {
		font-family: "Pretendard Variable", Pretendard, -apple-system, BlinkMacSystemFont, system-ui, Roboto, "Helvetica Neue", "Segoe UI", "Apple SD Gothic Neo", "Malgun Gothic", "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol", sans-serif;
		font-size: 14px;
		line-height: 1.3;
	}
}

.g-component {
	border: 1px solid #efefef;

	.g-component-area {
		padding: 20px;

		.g-component-wrap {
			display: flex;
			flex-wrap: wrap;
			gap: 8px;
			align-items: flex-start;

			.g-title-s {
				flex: 1 1 100%;

				&:not(:first-child) {
					margin-top: 12px
				}
			}

			.g-form-area {
				display: flex;
				gap: 8px;
				width: 100%;
			}

			.g-button-area {
				display: flex;
				flex-wrap: wrap;
				gap: 8px;
			}
		}
	}
}

.g-toggle {
	&:not(.active) {
		.toggle-body {
            display: none;
        }
	}

	.toggle-wrap {
		.toggle-head {
			.btn-toggle {
				display: flex;
				justify-content: space-between;
				align-items: center;
				position: relative;
				width: 100%;
				padding: 12px;
				background-color: #f7f7f7;
				font-size: 16px;
				font-weight: 500;
				text-align: left;

				&::after {
					content: '';
                    display: block;
                    width: 24px;
                    height: 24px;
                    background-color: #000;
                    border-radius: 50%;
					mask: icon(chevron-down);
				}
			}
		}
	}
}

.g-data-list {
	display: flex;
	flex-direction: column;
	row-gap: 16px;

	.data-item {
        flex: 1 1 100%;
		display: flex;
		flex-direction: column;
		row-gap: 8px;
		position: relative;
		padding-left: 16px;
		box-sizing: border-box;

		&::before {
			content: '';
			display: block;
			position: absolute;
			top: 0;
			left: 0;
			width: 16px;
			height: 16px;
			mask: icon(bullet-dot) no-repeat center/cover;
			background-color: #000;
		}
    }
}