@charset "UTF-8";
/********************************************************************************************************************************

 * Web Font

********************************************************************************************************************************/
/*
 * Pretendard 400, 500. 700
 */
@import url("https://cdn.jsdelivr.net/gh/orioncactus/pretendard@v1.3.9/dist/web/static/pretendard-dynamic-subset.min.css");
@import url("https://cdn.jsdelivr.net/gh/orioncactus/pretendard@v1.3.9/dist/web/variable/pretendardvariable-gov-dynamic-subset.min.css");
/********************************************************************************************************************************

 * Reset

********************************************************************************************************************************/
body, div, h1, h2, h3, h4, h5, h6, ul, ol, li, dl, dt, dd, p, span, a, table, thead, tfoot, tbody, th, td, form, legend, label, input, textarea, address, strong, em, pre, q, abbr, acronym, cite, blockquote, object, iframe, figure {
  margin: 0;
  padding: 0;
  font-style: normal;
}

body {
  font-family: "Pretendard Variable", Pretendard, -apple-system, BlinkMacSystemFont, system-ui, Roboto, "Helvetica Neue", "Segoe UI", "Apple SD Gothic Neo", "Malgun Gothic", "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol", sans-serif;
  font-size: 16px;
  font-weight: 400;
  line-height: 1;
  /*letter-spacing: -0.5px;*/
  -webkit-text-size-adjust: none;
}

img, fieldset {
  margin: 0;
  padding: 0;
  border: 0;
}

img {
  max-width: 100%;
  vertical-align: top;
}

ul, ol, li {
  list-style-type: none;
  list-style: none;
}

select, input, textarea {
  font-family: inherit;
  vertical-align: middle;
}

table {
  border-collapse: collapse;
}

legend, caption {
  overflow: hidden;
  width: 0;
  height: 0;
  line-height: 0;
  font-size: 0;
  visibility: hidden;
}

abbr {
  text-decoration: none;
}

a {
  font: inherit;
  color: inherit;
}
a:link, a:visited, a:active, a:focus {
  text-decoration: none;
}

input[type=text], input[type=password], input[type=email], input[type=tel], input[type=checkbox], input[type=radio], input[type=number] {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  border-radius: 0;
  appearance: none;
}

textarea, select {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  border-radius: 0;
  appearance: none;
}

button {
  overflow: visible;
  margin: 0;
  padding: 0;
  background-color: transparent;
  border: none;
  cursor: pointer;
  font: inherit;
  color: inherit;
  appearance: none;
}

::selection {
  background-color: #121212;
  color: #fff;
}

/********************************************************************************************************************************

 * color

********************************************************************************************************************************/
/********************************************************************************************************************************

 * typography

********************************************************************************************************************************/
@media (max-width: 1023px) {
  body {
    overflow-x: hidden;
    overflow-y: auto;
  }
}
@media (min-width: 1024px) {
  body {
    -webkit-overflow-scrolling: touch;
    overflow-x: hidden;
    overflow-y: overlay;
  }
  body::-webkit-scrollbar-thumb {
    border-width: 4px;
    border-style: solid;
    border-color: transparent;
    background-clip: content-box;
    background-color: rgba(0, 0, 0, 0.2);
  }
  body::-webkit-scrollbar-thumb:hover {
    background-color: rgba(0, 0, 0, 0.4);
  }
  body::-webkit-scrollbar-thumb:active {
    background-color: rgba(0, 0, 0, 0.6);
  }
  body::-webkit-scrollbar {
    width: 16px;
    height: 16px;
  }
  body::-webkit-scrollbar-thumb {
    border-radius: 8px;
  }
}
body:has(.g-navi-func.checked-within) {
  overflow-y: hidden;
}

#g-wrap {
  display: flex;
