  height: 50px;
  background-color: var(--color-base-000);
}
.skip a {
  display: flex;
  justify-content: center;
  align-items: center;
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  background-color: var(--color-base-000);
  color: var(--color-base-100);
  text-decoration: underline;
  text-align: center;
}
.skip a:not(:focus) {
  top: -50px;
}
.skip:focus-within {
  top: 0;
}

