.g-folder-tree {
  display: flex;
  gap: 80px;
}
.g-folder-tree p {
  font-size: 18px;
  font-weight: 700;
}
.g-folder-tree .tree-list {
  padding-left: 10px;
}
.g-folder-tree .tree-list li {
  position: relative;
  line-height: 1.7;
  padding: 0 0 0 44px;
}
.g-folder-tree .tree-list li::before {
  content: "└ ";
  position: absolute;
  top: 2px;
  left: 0;
  display: block;
}
.g-folder-tree .tree-list li::after {
  content: "🗂️ ";
  position: absolute;
  top: 1px;
  left: 20px;
  display: block;
}
.g-folder-tree .tree-list .tree-depth {
  padding: 0 0 0 0;
}
.g-folder-tree .tree-list .tree-depth:last-child li {
  margin-left: -20px;
}

.highlight-js {
  margin: 0;
  padding: 0;
}
.highlight-js .hljs {
  padding: 16px;
}
.highlight-js code {
  font-family: "Pretendard Variable", Pretendard, -apple-system, BlinkMacSystemFont, system-ui, Roboto, "Helvetica Neue", "Segoe UI", "Apple SD Gothic Neo", "Malgun Gothic", "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol", sans-serif;
  font-size: 14px;
  line-height: 1.3;
}

.g-component {
  border: 1px solid #efefef;
}
.g-component .g-component-area {
  padding: 20px;
}
.g-component .g-component-area .g-component-wrap {
  display: flex;
  flex-wrap: wrap;
  gap: 8px;
  align-items: flex-start;
}
.g-component .g-component-area .g-component-wrap .g-title-s {
  flex: 1 1 100%;
}
.g-component .g-component-area .g-component-wrap .g-title-s:not(:first-child) {
  margin-top: 12px;
}
.g-component .g-component-area .g-component-wrap .g-form-area {
  display: flex;
  gap: 8px;
  width: 100%;
}
.g-component .g-component-area .g-component-wrap .g-button-area {
  display: flex;
  flex-wrap: wrap;
  gap: 8px;
}

.g-toggle:not(.active) .toggle-body {
  display: none;
}
.g-toggle .toggle-wrap .toggle-head .btn-toggle {
  display: flex;
  justify-content: space-between;
  align-items: center;
  position: relative;
  width: 100%;
  padding: 12px;
  background-color: #f7f7f7;
  font-size: 16px;
  font-weight: 500;
  text-align: left;
}
.g-toggle .toggle-wrap .toggle-head .btn-toggle::after {
  content: "";
  display: block;
  width: 24px;
  height: 24px;
  background-color: #000;
  border-radius: 50%;
  mask: url("data:image/svg+xml;charset=utf8,%3Csvg xmlns='http://www.w3.org/2000/svg' viewBox='0 0 24 24'%3E %3Cpath fill='none' stroke='rgba(0,0,0,1)' stroke-width='1.5' d='M17.541 9.585 12.458 14.5 7.543 9.416'/%3E%3C/svg%3E");
}

.g-data-list {
  display: flex;
  flex-direction: column;
  row-gap: 16px;
}
.g-data-list .data-item {
  flex: 1 1 100%;
  display: flex;
  flex-direction: column;
  row-gap: 8px;
  position: relative;
  padding-left: 16px;
  box-sizing: border-box;
}
.g-data-list .data-item::before {
  content: "";
  display: block;
  position: absolute;
  top: 0;
  left: 0;
  width: 16px;
  height: 16px;
  mask: url("data:image/svg+xml;charset=utf8,%3Csvg xmlns='http://www.w3.org/2000/svg' viewBox='0 0 24 24'%3E %3Cpath fill='rgba(0,0,0,1)' fill-rule='evenodd' d='M12 11C13.1046 11 14 11.8954 14 13C14 14.1046 13.1046 15 12 15C10.8954 15 10 14.1046 10 13C10 11.8954 10.8954 11 12 11Z' clip-rule='evenodd'/%3E%3C/svg%3E") no-repeat center/cover;
  background-color: #000;
}